.home{
    display: flex;
    flex-direction: column;
    align-items: center;
    max-width: 1500px;
}
.home__image{
    width: 100vw;
    height: 300px;
    object-fit: cover;
    z-index:-1;
    margin-bottom: -150px;
    mask-image: linear-gradient(to bottom,rgba(0,0,0,1),rgba(0,0,0,0));
}
.home__row{
    display: flex;
    flex-direction: row;
    flex-grow: 1;
}
